import React from "react";
import { useSortable } from "@dnd-kit/sortable";
import { CSS } from "@dnd-kit/utilities";
import SOCard from "./SOCard";

export default function SortableCardWrapper({ id, data }) {
  const {
    attributes,
    listeners,
    setNodeRef,
    transform,
    transition,
    isDragging
  } = useSortable({ id });

  // prevent crash if data missing
  if (!data) {
    console.warn("SortableCardWrapper received NO data for id:", id);
    return null;
  }

  const style = {
    transform: CSS.Transform.toString(transform),
    transition,
    opacity: isDragging ? 0.7 : 1
  };

  return (
    <div
      ref={setNodeRef}
      style={style}
      {...attributes}
      {...listeners}
      className={`rounded-xl shadow bg-white ${isDragging ? "scale-[1.03]" : ""}`}
    >
      <SOCard item={data} dragging={isDragging} />
    </div>
  );
}
