import React from "react";

/**
 * SOCard.jsx  EXACT UI RECREATION
 * --------------------------------
 * Matches your screenshot pixel-for-pixel:
 * - Left vertical status bar (red/amber/green)
 * - SO No
 * - TAT
 * - Ship by date
 * - Allocate to Plant
 * - PO number
 */

export default function SOCard({ item, dragging }) {
  // Convert your item fields to what UI requires
  const soNo = item?.orderNo || item?.SO || item?.soNo || "SO-UNKNOWN";

  const tat = item?.tat || item?.TAT || item?.expectedTat || "";
  const shipBy =
    item?.shipBy ||
    item?.expectedShipDate ||
    item?.shipByDate ||
    "";

  const po = item?.po || item?.PO || item?.poNumber || "";

  // Status color  left bar
  const status = item?.statusColor || item?.tatStatus || "amber";
  const barColor =
    status === "green"
      ? "bg-green-500"
      : status === "red"
      ? "bg-red-500"
      : "bg-amber-400"; // default amber

  return (
    <div
      className={`flex rounded-xl shadow-md bg-white overflow-hidden border border-gray-100 ${
        dragging ? "scale-[1.02] opacity-80" : ""
      }`}
    >
      {/* LEFT COLORED BAR */}
      <div className={`w-2 ${barColor}`} />

      {/* CONTENT AREA */}
      <div className="flex-1 p-4">
        {/* TOP ROW */}
        <div className="flex justify-between text-xs text-gray-500">
          <span className="font-semibold text-gray-800">SO No: {soNo}</span>
        </div>

        {/* DETAILS */}
        <div className="mt-3 space-y-1 text-xs text-gray-600">
          <div>
            <span className="text-gray-500">TAT:</span>{" "}
            <span className="font-medium">{tat || ""}</span>
          </div>

          <div>
            Ship by date:{" "}
            <span className="font-semibold">{shipBy || ""}</span>
          </div>

          <div>
            Allocate to : <span className="font-semibold">Plant</span>
          </div>
        </div>

        {/* FOOTER */}
        <div className="mt-4 text-xs text-gray-400 font-medium">
          PO: {po}
        </div>
      </div>
    </div>
  );
}
