import React from "react";
import {
  SortableContext,
  verticalListSortingStrategy
} from "@dnd-kit/sortable";
import { useDroppable } from "@dnd-kit/core";
import SortableCardWrapper from "./SortableCardWrapper";
import SOCard from "./SOCard";

/**
 * Column.jsx  EXACT MATCH to your UI screenshot
 * ----------------------------------------------------
 * - Column header with title, counter badge, filter icon
 * - 320px fixed width (w-80)
 * - Soft white background + subtle shadow
 * - Clean spacing for cards
 * - Proper vertical sorting behavior
 */

export default function Column({ stage, items = [] }) {
  const { id, title } = stage;
  const { setNodeRef } = useDroppable({ id });

  return (
    <section
      ref={setNodeRef}
      id={id}
      className="w-80 bg-white rounded-xl p-4 shadow-md min-h-[220px] flex flex-col"
    >
      {/* HEADER */}
      <div className="flex items-center mb-4">
        <h4 className="font-semibold text-gray-800">{title}</h4>

        {/* Counter Badge */}
        <div className="ml-2 w-6 h-6 rounded-full bg-gray-100 flex items-center justify-center text-xs font-semibold text-gray-600 shadow-sm">
          {items.length}
        </div>

        {/* Filter Icon (right side) */}
        <div className="ml-auto cursor-pointer opacity-60 hover:opacity-100">
          &#9881;
        </div>
      </div>

      {/* CARD LIST */}
      <SortableContext
        items={items.map((i) => String(i.ID))}
        strategy={verticalListSortingStrategy}
      >
        <div className="space-y-4">
          {items.length === 0 && (
            <div className="text-center text-gray-400 text-sm py-4">
              No orders
            </div>
          )}

          {items.map((order) => (
            <SortableCardWrapper
              key={order.ID}
              id={String(order.ID)}
              data={order}
            >
              <SOCard item={order} />
            </SortableCardWrapper>
          ))}
        </div>
      </SortableContext>
    </section>
  );
}
