import React, { useState } from "react";
import {
  DndContext,
  PointerSensor,
  useSensor,
  useSensors,
  DragOverlay,
  closestCorners
} from "@dnd-kit/core";
import Column from "./Column";
import SOCard from "./SOCard";

/**
 * Board.jsx  Fully aligned to your exact UI:
 * - 5 fixed columns
 * - Scrollable horizontal board
 * - Accurate spacing & drag behavior
 * - Safe drop zones (only columns can receive a card)
 */

export default function Board({ stages = [], ordersByStage = {}, onMove }) {
  const sensors = useSensors(useSensor(PointerSensor));
  const [activeCard, setActiveCard] = useState(null);

  const handleDragStart = (event) => {
    const { active } = event;
    setActiveCard(active.data?.current || null);
  };

  const handleDragEnd = (event) => {
    const { active, over } = event;
    setActiveCard(null);

    if (!over) return;

    const draggedId = active.id;
    const toStage = over.id; // Only column IDs allowed

    // Prevent dropping on cards
    if (!stages.some((s) => s.id === toStage)) return;

    if (typeof onMove === "function") onMove(draggedId, toStage);
  };

  return (
    <DndContext
      sensors={sensors}
      onDragStart={handleDragStart}
      onDragEnd={handleDragEnd}
      collisionDetection={closestCorners}
    >
      {/*  EXACT horizontal scroll board */}
      <div className="flex gap-8 overflow-x-auto py-2 board-scroll">

        {stages.map((stage) => (
          <Column
            key={stage.id}
            stage={stage}
            items={ordersByStage[stage.id] || []}
          />
        ))}

      </div>

      {/* Ghost card while dragging */}
      <DragOverlay>
        {activeCard ? (
          <div style={{ width: 300 }}>
            <SOCard item={activeCard} dragging />
          </div>
        ) : null}
      </DragOverlay>
    </DndContext>
  );
}
