import React, { useCallback, useEffect, useState } from "react";
import Sidebar from "./components/Sidebar";
import Board from "./components/Board";
import Topbar from "./components/Topbar";
import sampleData from "./data/sampleData";

/**
 * WORKFLOW MODE: sampleData-first
 * - Forces the UI to use local sampleData for quick UI work.
 * - No backend calls are made.
 * - Later: flip a flag to re-enable real backend.
 */

/** Final Kanban mapping (visual labels) */
const STAGES = [
  { id: "SalesSupport", title: "Sales Order" },
  { id: "Procurement", title: "Purchases" },
  { id: "RMInventory", title: "RM Inventory" },
  { id: "Quality", title: "Quality" },
  { id: "FGInventory", title: "FG Inventory" }
];

export default function App() {
  const [ordersByStage, setOrdersByStage] = useState({});
  const [loading, setLoading] = useState(true);
  const [error] = useState(null);

  // Normalizer: build buckets from sample data items
  const normalize = (orders = []) => {
    const result = {};
    for (const s of STAGES) result[s.id] = [];

    orders.forEach(o => {
      // sampleData items might use `currentStage` or `stage`  normalize defensively
      const stage = o.currentStage || o.stage || "SalesSupport";
      if (result[stage]) result[stage].push(o);
      else result["SalesSupport"].push(o);
    });

    return result;
  };

  // Load sample data (synchronous)
  const loadOrders = useCallback(async () => {
    setLoading(true);
    try {
      // If sampleData is exported as an object with .value, handle both shapes
      const arr = Array.isArray(sampleData) ? sampleData : (sampleData.value || []);
      setOrdersByStage(normalize(arr));
    } catch (e) {
      console.warn("Failed to load sample data", e);
    } finally {
      setLoading(false);
    }
  }, []);

  useEffect(() => {
    loadOrders();
  }, [loadOrders]);

  // Handle drag move (in-memory only). ID property may be 'ID' or 'id' in sample data.
  const handleMove = async (orderID, toStage) => {
    let fromStage = null;
    for (const s of Object.keys(ordersByStage)) {
      if (ordersByStage[s].some(o => String(o.ID || o.id) === String(orderID))) {
        fromStage = s;
        break;
      }
    }
    if (!fromStage || fromStage === toStage) return;

    const snapshot = JSON.parse(JSON.stringify(ordersByStage));
    const cloned = JSON.parse(JSON.stringify(ordersByStage));

    const sourceList = cloned[fromStage];
    const idx = sourceList.findIndex(o => String(o.ID || o.id) === String(orderID));
    const [moved] = sourceList.splice(idx, 1);

    moved.currentStage = toStage;
    cloned[toStage].unshift(moved);
    setOrdersByStage(cloned);

    // NOTE: sample mode  no backend persistence
    // Keep optimistic UX; if later enabled, calls will persist to backend.
  };

  return (
    <div className="h-screen flex flex-col">
      <Topbar />
      <div className="flex flex-1 overflow-hidden">
        <Sidebar />
        <main className="flex-1 p-6 overflow-auto">
          {error && <div className="text-red-600 text-sm mb-2">{error}</div>}
          {loading ? (
            <div className="text-gray-600 text-lg">Loading sample orders</div>
          ) : (
            <Board stages={STAGES} ordersByStage={ordersByStage} onMove={handleMove} />
          )}
        </main>
      </div>
    </div>
  );
}
